<template>
  <div>
    <span
      v-for="(e, i) in Fields"
      :key="i"
      :name="e.field"
      :style="`${e.contentAlign?'text-align:'+e.contentAlign+';':''}width: ${e.width || 90}px;${e.textColor?'color: '+e.textColor + ';':''}${item[e.field + 'Style']};border-right:1px solid ${theme.tableContentBdColor};${getColor(e)}`"
    >
      <template>{{item && format(e) || '--'}}</template>
    </span>
  </div>
</template>
<script>
import utils from '../../utils/index';
export default {
  name: 'list-cell',
  props: {
    Fields: {
      type: Array,
      required: true,
    },
    theme: {
      type: Object,
      required: true,
    },
    item: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    start: {
      type: Number,
      required: true,
    },
    lastData: {
      type: Object,
      required: false,
    },
  },
  methods: {
    getColor(e) {
      let color = '';
      let { textColor, updownStyle, compareField, field, strategy } = e;
      let { ZuiXinJia, ZuoShou } = this.item;
      if (textColor) {
        color = textColor;
      } else if (updownStyle === true) {
        compareField = compareField || 'ZuoShou';
        if (compareField === 'ZuoShou') {
          let diff = 0;
          if (
            [
              'ZuiXinJia',
              'KaiPanJia',
              'ZuiGaoJia',
              'ZuiDiJia',
              'WeiTuoMaiRuJia1',
              'WeiTuoMaiChuJia1',
              'JunJia',
            ].indexOf(field) > -1
          ) {
            diff = this.item[field] - ZuoShou;
          } else if (['ZuiXinJia', 'ZhangFu', 'ZhangDie'].indexOf(field) > -1) {
            diff = ZuiXinJia - ZuoShou;
          } else if (
            ['ZhangSu', 'WeiCha', 'WeiBi', 'FenZhongZhangFu5'].indexOf(field) >
            -1
          ) {
            diff = this.item[field];
          }
          if (diff > 0) {
            color = this.theme.redColor;
          } else if (diff < 0) {
            color = this.theme.greenColor;
          } else {
            color = this.theme.normalColor;
          }
        }
      } else if (strategy === 'xianshou') {
        color = this.theme.normalColor;
        if (this.lastData) {
          if (this.item.ZuiXinJia <= this.lastData.WeiTuoMaiRuJia1) {
            color = this.theme.greenColor;
          } else if (this.item.ZuiXinJia >= this.lastData.WeiTuoMaiChuJia1) {
            color = this.theme.redColor;
          } else if (this.item.ZuiXinJia === this.lastData.ZuiXinJia) {
            color = this.theme.normalColor;
          } else if (this.item.ZuiXinJia > this.lastData.ZuiXinJia) {
            color = this.theme.redColor;
          } else if (this.item.ZuiXinJia < this.lastData.ZuiXinJia) {
            color = this.theme.greenColor;
          }
        }
      } else {
        return '';
      }
      return `color:${color};`;
    },
    format(e) {
      if (!this.item || JSON.stringify(this.item) === '{}' || !e.field) {
        return '--';
      }
      let { field, unit, ratio, format, precision } = e;
      let value = this.item[field];
      if (format) {
        return e.format(this.item, e);
      }
      if (ratio) value *= ratio;
      switch (field) {
        case 'XuHao':
          value = this.start + this.index + 1;
          break;
        case 'Obj':
          value = (this.item.Obj || '').slice(2) || '--';
          break;
        case 'ZhongWenJianCheng':
        case 'HangYe':
          break;
        case 'ZuiXinJia':
        case 'ZuoShou':
        case 'KaiPanJia':
        case 'ZuiGaoJia':
        case 'ZuiDiJia':
        case 'WeiTuoMaiRuJia1':
        case 'WeiTuoMaiChuJia1':
        case 'JunJia':
        case 'ZhangFu':
        case 'FenZhongZhangFu5': // 涨速%
        case 'WeiBi':
        case 'HuanShou':
          // value = this.formatRatio(value, null, false);
          // break;
          value = this.formatPrice(value, precision);
          break;
        case 'ZhangDie':
          value = this.formatPrice(value, precision, true);
          break;
        case 'ShiYingLv':
        case 'ShiJingLv':
        case 'ShiXiaoLv':
          value = this.formatPrice(value, null, false);
          break;
        case 'ChengJiaoLiang':
        case 'XianShou':
          value = this.formatVolume(value, 100, 2);
          break;
        case 'NeiPan':
        case 'WaiPan':
          // debugger
          value = this.formatStockText(value / 100, 2, unit, true, true, true);
          // value = Math.abs(value)
          break;
        case 'ChengJiaoE':
        case 'ZongShiZhi':
        case 'LiuTongShiZhi':
        case 'WeiCha':
          value = this.formatVolume(value, 1, 2);
          break;
        default:
          value = this.formatPrice(value);
          break;
        //
      }
      return value;
    },
    formatStockText: utils.formatStockText,
    formatPrice: utils.formatPrice,
    formatRatio: utils.formatRatio,
    formatColor: utils.formatColor,
    formatVolume: utils.formatVolume,
  },
};
</script>
