<template>
  <div class="largelist-demo">
    <large-list
    ref="largeList"
      :showTitle="false"
      :showScroller="false"
      :titleHeight="titleHeight"
      :contentHeight="contentHeight"
      :contentWidth="contentWidth"
      :theme="theme"
      :fieldsConfig="fieldsConfig"
      :objReqParam="objReqParam"
      :cacheDATA="cacheData"
      :objListCache="objListCache"
      :focusedOBJ="''"
      @right-click="rightclick"
      @dbl-click="dblclick"
      @left-click="leftclick"
      @fieldsConfig-ischange="fieldsConfigChange"
      @focused-ischange="focusedObjIschange"
      @cachedata-ischange="cachedataIschange"
      @objlist-ischange="objlistIschange"
    >
      <!--

      <template slot-scope="slotScopes">
        <span
          v-for="(e, i) in fields"
          :key="i"
          :style="{
            height: slotScopes.itemHeight/2 + 'px',
            lineHeight: slotScopes.itemHeight/2 + 'px',
            backgroundColor:
            slotScopes.focusedObj === slotScopes.data.Obj ? theme.tableContentActiveBgColor:
            theme.tableContentBgColor}"
        >{{slotScopes.data[e] || '--'}}</span>
      </template> -->
    </large-list>
  </div>
</template>

<script>
import { theme, fieldsConfig } from '../utils/fieldsConfig.js';
export default {
  name: 'largelist-demo',
  data() {
    return {
      theme,
      fieldsConfig,
      contentHeight: 0,
      contentWidth: document.documentElement.clientWidth,
      titleHeight: 40,
      focusedObj: '',
      objListCache: {},
      cacheData: {},
    };
  },
  props: {
    objReqParam: {
      type: Object,
    },
  },
  created() {
    this.contentHeight =
      document.documentElement.clientHeight - this.titleHeight;
    this.resizeFunc = this.$Utils.debounce(() => {
      this.contentHeight =
        document.documentElement.clientHeight - this.titleHeight;
      this.contentWidth = document.documentElement.clientWidth;
    }, 300);
    window.addEventListener('resize', this.resizeFunc);
    window.setTimeout(() => {
      this.$refs.largeList.initState();
    });
  },
  methods: {
    initState() {
      this.$refs.largeList.initState();
    },
    rightclick(obj) {
      console.log('right-click', obj);
    },
    dblclick(obj) {
      console.log('dbl-click', obj);
    },
    leftclick(obj) {
      console.log('left-click', obj);
    },
    fieldsConfigChange(newFieldsConfig) {
      this.fieldsConfig = newFieldsConfig;
    },
    focusedObjIschange(focusedObj, focusedIndex) {
      this.focusedObj = focusedObj;
      this.focusedIndex = focusedIndex;
    },
    cachedataIschange(cacheData) {
      // console.log(cacheData);
      this.cacheData = cacheData;
    },
    objlistIschange(objList) {
      let key = this.$Utils.getKeyFromParams(this.objReqParam);
      this.objListCache[key] = objList;
    },
  },
  watch: {
    objReqParam() {
      this.$refs.largeList.initState();
    },
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resizeFunc);
  },
};
</script>
<style lang="less">
.largelist-demo {
  width: 100%;
  height: 100%;
}
</style>
