<template>
  <div class="small-list-wrap">
    <div
      class="small-list"
      id="smalist"
    >
      <div
        class="smalist-title"
        v-if="showTitle"
        :style="{backgroundColor: theme.tableTitleBgColor, color: theme.tableTitleColor, borderBottom: '1px solid ' + theme.tableTitleBdColor}"
      >
        <span
          class="th"
          v-for="(field, index) in fields"
          :key="index"
          :name="field"
          :style="`width: ${100/fields.length}%;border-right: 1px solid ${theme.tableTitleBdColor};text-align:${titleAlign}`"
          @click="reSort"
        >
          {{title[field]}}
          <i
            v-if="ORDERBY===field"
            :class="`iconfont icon-arrow-${DESC === 2?'up':''}${DESC === 1?'down':''}`"
          >
          </i>
        </span>
      </div>
      <div
        class="smalist-content"
        id="smallcontent"
        :style="{color: theme.tableContentColor, backgroundColor: theme.tableContentBgColor, height: showTitle?'calc(100%-30px)':'100%'}"
        @mousewheel.passive="mousewheel"
      >
        <template v-if="dataList.length">
          <div
            class="smalist-item"
            v-for="(item, index) in dataList"
            :key="index"
            :style="{textAlign: contentAlign,borderBottom: '1px solid ' + theme.tableContentBdColor, backgroundColor: focusedObj === item.Obj ? theme.tableContentActiveBgColor: hoverObj === item.Obj ? theme.tableContentHoverBgColor : theme.tableContentBgColor, height: itemHeight + 'px', lineHeight: itemHeight + 'px'}"
            :name="item.Obj"
            @contextmenu.prevent
            @mouseup="mouseup($event, item.Obj)"
            @mouseover="hoverObj = item.Obj"
            @mouseleave="hoverObj = ''"
          >
            <slot
              :data="item"
              :itemHeight="itemHeight"
              :focusedObj="focusedObj"
            >
              <span
                v-for="(e, i) in fields"
                :key="i"
                :style="`${item[e + 'Style']};width: ${100/fields.length}%;border-right: 1px solid ${theme.tableContentBdColor}`"
              >
                {{format(item, e)}}
              </span>
            </slot>
          </div>
        </template>
        <template v-else>
          <slot name="loading"></slot>
        </template>
      </div>
    </div>
    <div
      class="table-scroller"
      v-if="showScroller"
      :style="{backgroundColor: theme.scrollerBgColor2, color: theme.scrollerBgColor2}"
    >
      <div
        class="scroller-top"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: 'transparent transparent'+ theme.scrollerBgColor3 +' transparent'}"></i></div>
      <div
        class="scroller-bottom"
        id="scrollerBottom"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: theme.scrollerBgColor3 + ' transparent transparent transparent'}"></i></div>
      <div
        class="scroller-scrolling"
        id="scroller"
        :style="{backgroundColor: theme.scrollerBgColor1}"
        @mousedown="scrolling"
      ></div>
    </div>
  </div>
</template>
<script>
import utils from '../../utils/index';
export default {
  name: 'small-list',
  data() {
    return {
      start: 0, // 显示数据的起始下标
      count: 0, // 显示的数据行数
      itemHeight: 0, // 每一行的行高
      objList: [], // 缓存所有list
      dataList: [], // 展示在页面中的list数据
      cacheData: {}, // 缓存数据
      activeIndex: -1, // 记录当前选中的行的下标
      focusedObj: '', // 记录当前选中的 Obj
      hoverObj: '', //
      objArr: [],
      DESC: 0,
      ORDERBY: '',
      i: 1,
    };
  },
  props: {
    theme: Object,
    title: Object,
    titleAlign: {
      type: String,
      validator(value) {
        return utils.oneOf(value, ['left', 'center', 'right']);
      },
      default: 'center',
    },
    contentAlign: {
      type: String,
      validator(value) {
        return utils.oneOf(value, ['left', 'center', 'right']);
      },
      default: 'center',
    },
    // 整体列表的请求参数
    objReqParam: {
      type: Object,
      default() {
        return {
          gql: 'block=市场/沪深市场/沪深A股',
          // market: 'SH',
          // obj: 'SH600519',
          field: 'ZhongWenJianCheng',
        };
      },
    },
    // 整体列表的默认排序
    desc: {
      type: Number,
      validator(value) {
        return utils.oneOf(value, [0, 1, 2]);
      },
      default: 0, // 0 不排序, 1: true正序, 2: false倒序
    },
    // 整体列表的排序字段
    orderby: {
      type: String,
      default: 'ZhangFu',
    },
    // 局部列表的展示字段
    fields: {
      type: Array,
      default() {
        return ['Obj', 'ZhongWenJianCheng', 'ZhangDie'];
      },
    },
    // 是否显示表头
    showTitle: {
      type: Boolean,
      default: true,
    },
    // 是否显示上下滚动条
    showScroller: {
      type: Boolean,
      default: true,
    },
    // 每一份数据的高度
    height: {
      type: Number,
      default: 30,
    },
    precision: {
      type: Number,
      default: 2,
    },
  },
  watch: {
    focusedObj(newVal) {
      this.$emit('focused-ischange', newVal);
    },
  },
  created() {
    this.init();
  },
  mounted() {
    window.setTimeout(() => {
      // 对父组件传过来的高度进行处理
      let H = document.querySelector('#smallcontent').offsetHeight;
      this.showTitle && (H -= 30);
      document.querySelector('#smallcontent').style.height = H + 'px';
      this.count = Math.ceil(H / this.height);
      this.itemHeight = H / this.count;

      if (this.showScroller) {
        this.topMax =
          document.querySelector('#smalist').offsetHeight -
          document.querySelector('#scroller').offsetHeight -
          (document.querySelector('#scrollerBottom').offsetHeight + 1);
        this.topMin =
          document.querySelector('#scrollerBottom').offsetHeight + 1;
      }
    }, 20);
    this.DESC = this.desc;
    this.ORDERBY = this.orderby;
  },
  methods: {
    init() {
      this.initData();
      if (!this.objReqParam.obj) {
        this.updateObjListInterval = window.setInterval(() => {
          this.initData();
        }, 5000);
      }
      this.keyupFunc = e => {
        e.preventDefault();
      };
      this.keydownFunc = e => {
        e.preventDefault();
        this.keydown(e.keyCode);
      };
      document.onkeyup = this.keyupFunc;
      document.onkeydown = this.keydownFunc;
    },
    format(item, field) {
      if (!item || JSON.stringify(item) === '{}' || !field) {
        return '--';
      }
      let value = item[field];
      switch (field) {
        case 'Obj':
          value = (value || '').slice(2) || '--';
          break;
        case 'ZhongWenJianCheng':
          break;
        case 'ZuiXinJia':
        case 'ZhangFu':
        case 'FenZhongZhangFu5': // 涨速%
          value = this.formatPrice(value, this.precision);
          break;
        case 'ZhangDie':
          value = this.formatPrice(value, this.precision, true);
          break;
        default:
          value = this.formatPrice(value);
          break;
      }
      return value;
    },
    mouseup(e) {
      let obj = e.target.parentElement.getAttribute('name');
      this.focusedObj = obj;
      if (e.button === 2) {
        this.$emit('right-click', obj);
      } else {
        this['timer' + this.i] = new Date().getTime();
        this.i += 1;
        if (this.timer1 && this.timer2 && this.timer2 - this.timer1 < 300) {
          if (window.mouseup_timer) {
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
          }
          this.$emit('dbl-click', obj);
          this.i = 1;
          this.timer1 = undefined;
          this.timer2 = undefined;
        } else if (this.timer1 && this.timer2 === undefined) {
          window.mouseup_timer = window.setTimeout(() => {
            this.$emit('left-click', obj);
            this.i = 1;
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
            this.timer1 = undefined;
            this.timer2 = undefined;
          }, 300);
        }
      }
    },
    reSort(e) {
      if (this.objReqParam.obj) {
        return;
      };
      let orderby = e.target.getAttribute('name');
      if (this.ORDERBY === orderby) {
        switch (this.DESC) {
          case 0:
            this.DESC = 1;
            break;
          case 1:
            this.DESC = 2;
            break;
          case 2:
            this.DESC = 0;
            break;
          default:
            break;
        }
      } else {
        this.ORDERBY = orderby;
        this.DESC = 1;
      }
      this.initData();
    },
    initData() {
      this.getObjList()
        .then(res => {
          this.objList = res;
          this.updateDataList();
        })
        .catch(err => {
          console.log('small-list getObjList err: ' + err);
          // this.objList = objList;
          // this.updateDataList();
        });
    },
    // 根据 外部传递props中的 reqParam, orderby 和 DESC 返回排序后的列表顺序
    getObjList() {
      let indexsParams;
      if (!this.objReqParam.obj) {
        if (this.DESC === 0) {
          indexsParams = Object.assign(this.objReqParam, {
            desc: false,
            orderby: 'JiaoYiDaiMa',
          });
        } else {
          indexsParams = Object.assign(this.objReqParam, {
            desc: this.DESC === 1,
            orderby: this.ORDERBY,
          });
        }
      } else {
        indexsParams = this.objReqParam;
      }
      return new Promise((res, rej) => {
        this.dataQuery = this.$dzhyun.query('/stkdata', indexsParams, data => {
          if (data && !(data instanceof Error)) {
            res(data);
          } else {
            rej(data);
          }
        });
      });
    },
    // 根据 this.start, this.count 和 外部传递的 fields 渲染出当前屏数据:
    // 1. 数据拿到之前, 先从缓存中得到对应数据并渲染
    // 2. 数据拿到之后, 如果是多条数据, 直接更新缓存中的数据同时渲染页面数据
    //    如果是单条数据, 只更新缓存中的数据, 100ms 后在从缓存中得到页面数据
    updateDataList() {
      this.cancelSubscribe();
      this.objArr = this.objList
        .slice(this.start, this.start + this.count)
        .map(e => e.Obj);
      let indexsParams = {
        obj: this.objArr.join(','),
        field: this.fields,
      };
      this.setDataFromCache();
      this.dataSubscribe = this.$dzhyun.subscribe(
        '/stkdata',
        indexsParams,
        data => {
          if (data && !(data instanceof Error)) {
            if (data.length === 1) {
              this.setCacheData(data[0]);
              if (window.cacheTimer) {
                window.clearTimeout(window.cacheTimer);
                window.cacheTimer = null;
              }
              window.cacheTimer = window.setTimeout(() => {
                this.setDataFromCache();
              }, 100);
              return;
            }
            for (let i = 0, l = data.length; i < l; i++) {
              let newDataItem = data[i];
              this.setDataItem(newDataItem);
              this.setCacheData(newDataItem);
            }
          }
        }
      );
    },
    setDataFromCache() {
      this.objArr.forEach((e, i) => {
        let dataItem = this.dataList.find(
          dataItem => dataItem && dataItem.Obj === e
        );
        let cacheItem = this.cacheData[e];
        if (
          dataItem &&
          cacheItem &&
          dataItem.ZuiXinJia !== cacheItem.ZuiXinJia
        ) {
          cacheItem.ZuiXinJiaStyle = cacheItem.ZhangDieStyle = cacheItem.ZhangFuStyle = `animation: twinkling${
            dataItem.ZuiXinJia > cacheItem.ZuiXinJia ? 'Green' : 'Red'
          } 1s ease-in 0s;`;
        }
        this.$set(this.dataList, i, cacheItem || {});
      });
    },
    setDataItem(newDataItem) {
      let index = this.objArr.findIndex(e => e === newDataItem.Obj);
      this.$set(this.dataList, index, newDataItem);
    },
    setCacheData(newDataItem) {
      this.cacheData[newDataItem.Obj] = newDataItem;
    },
    cancelQuery() {
      if (this.dataQuery) {
        this.dataQuery.cancel();
        this.dataQuery = null;
      }
    },
    cancelSubscribe() {
      if (this.dataSubscribe) {
        this.dataSubscribe.cancel();
        this.dataSubscribe = null;
      }
    },
    mousewheel(e) {
      const maxStartVal = this.objList.length - this.count;
      this.start -= Math.round(e.wheelDelta / 120);
      this.start = Math.min(maxStartVal, this.start);
      this.start = Math.max(0, this.start);
      this.changeTopFromStart();
      this.updateDataList();
      if (this.activeIndex !== -1) {
        document.querySelector('.smalist-content').childNodes[
          this.activeIndex
        ].style.backgroundColor = this.theme.tableContentBgColor;
        this.activeIndex = this.objArr.findIndex(
          e => e.Obj === this.focusedObj
        );
        if (this.activeIndex != -1) {
          document.querySelector('.smalist-content').childNodes[
            this.activeIndex
          ].style.backgroundColor = this.theme.tableContentActiveBgColor;
        }
      }
    },
    changeTopFromStart() {
      if (this.showScroller) {
        let bottomHeight = document.querySelector('#scrollerBottom')
          .offsetHeight;
        document.querySelector('#scroller').style.top =
          (this.start / (this.objList.length - this.count)) *
            (this.topMax - (bottomHeight + 1)) +
          (bottomHeight + 1) +
          'px';
      }
    },
    scrolling(e) {
      let target = e.target;
      // 鼠标点击物体那一刻相对于物体上侧边框的距离=点击时的位置相对于浏览器最上边的距离-物体上边框相对于浏览器最左边的距离
      var diffY = e.clientY - target.offsetTop;
      let mousemove = e => {
        let top = e.clientY - diffY;
        top = Math.max(this.topMin, top);
        top = Math.min(this.topMax, top);
        let scrollerY = top - this.topMin;
        let originStart = this.start;
        let maxStartVal = this.objList.length - this.count;
        // this.start / maxStartVal = scrollerY / (this.topMax - this.topMin)
        this.start = Math.round(
          (scrollerY / (this.topMax - this.topMin)) * maxStartVal
        );
        this.start = Math.min(maxStartVal, this.start);
        this.start !== originStart && this.updateDataList();
        target.style.top = top + 'px';
      };
      let mouseup = () => {
        document.removeEventListener('mousemove', mousemove);
        document.removeEventListener('mouseup', mouseup);
      };
      document.addEventListener('mousemove', mousemove, { passive: true });
      document.addEventListener('mouseup', mouseup);
    },
    changeActiveItem(new_index) {
      let childNodes = document.querySelector('.smalist-content').childNodes;
      if (new_index === this.activeIndex) return;
      if (this.activeIndex !== -1) {
        childNodes[
          this.activeIndex
        ].style.backgroundColor = this.theme.tableContentBgColor;
      }
      childNodes[
        new_index
      ].style.backgroundColor = this.theme.tableContentActiveBgColor;
      this.activeIndex = new_index;
    },
    // 监听 keydown 事件,
    // 上下键：改变 start 值后, 调用 this.updateDataList, 以更新页面数据 和 滚动条位置, 并更改高亮行
    keydown(code) {
      const maxStartVal = this.objList.length - this.count;
      let originStart = this.start;
      let index;
      switch (code) {
        case 38:
          // 上箭头
          index = this.objArr.findIndex(e => e === this.focusedObj);
          if (index > 0) {
            this.focusedObj = this.objArr[index - 1];
          } else {
            this.start = Math.max(0, this.start - 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
            }
            if (index === 0) {
              this.focusedObj = this.objArr[0];
            }
          }
          break;
        case 40:
          // 下箭头
          index = this.objArr.findIndex(e => e === this.focusedObj);
          if (index > -1 && index < this.objArr.length - 1) {
            this.focusedObj = this.objArr[index + 1];
          } else {
            this.start = Math.min(maxStartVal, this.start + 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
            }
            if (index === this.objArr.length - 1) {
              this.focusedObj = this.objArr[this.objArr.length - 1];
            }
          }
          break;
        case 33:
          // 上页
          this.start = Math.max(0, this.start - this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 34:
          // 下页
          this.start = Math.min(maxStartVal, this.start + this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 35:
          // end
          this.start = maxStartVal;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 36:
          // home
          this.start = 0;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        default:
          break;
      }
    },
    formatPrice: utils.formatPrice,
    formatRatio: utils.formatRatio,
    formatStockText: utils.formatStockText,
    formatColor: utils.formatColor,
  },
  activated() {
    this.init();
  },
  deactivated() {
    this.cancelQuery();
    this.cancelSubscribe();
    window.clearInterval(this.updateObjListInterval);
    document.onkeyup = null;
    document.onkeydown = null;
  },
  beforeDestroy() {
    this.cancelQuery();
    this.cancelSubscribe();
    window.clearInterval(this.updateObjListInterval);
    document.onkeyup = null;
    document.onkeydown = null;
  },
};
</script>
<style lang="less">
@import "../../styles/common.less";
@import "../../styles/small-list.less";
</style>
