<template>
  <div class="static-small-list-wrap">
    <div
      class="small-list"
      ref="smalist"
    >
      <div
        class="smalist-title"
        v-if="showTitle"
        :style="{backgroundColor: theme.tableTitleBgColor, color: theme.tableTitleColor, borderBottom: '1px solid ' + theme.tableTitleBdColor}"
      >
        <span
          class="th"
          v-for="(field, index) in fields"
          :key="index"
          :name="field"
          :style="`width: ${100/fields.length}%;border-right: 1px solid ${theme.tableTitleBdColor};text-align:${titleAlign}`"
        >
          {{title[field]}}
          <i
            v-if="ORDERBY===field"
            :class="`iconfont icon-arrow-${DESC === 2?'up':''}${DESC === 1?'down':''}`"
          >
          </i>
        </span>
      </div>
      <div
        class="smalist-content"
        ref="smallcontent"
        :style="{color: theme.tableContentColor, backgroundColor: theme.tableContentBgColor, height: showTitle?'calc(100%-30px)':'100%'}"
        @mousewheel.passive="mousewheel"
      >
        <template v-if="dataList.length">
          <div
            class="smalist-item"
            v-for="(item, index) in dataList"
            :key="index"
            :style="{textAlign: contentAlign,backgroundColor: focusedObj === item.DaiMa ? theme.tableContentActiveBgColor: hoverObj === item.DaiMa ? theme.tableContentHoverBgColor : theme.tableContentBgColor, height: itemHeight + 'px', lineHeight: itemHeight + 'px'}"
            :name="item.DaiMa"
            @contextmenu.prevent
            @mouseup="mouseup($event, item.DaiMa)"
            @mouseover="hoverObj = item.DaiMa"
            @mouseleave="hoverObj = ''"
          >
            <slot
              :data="item"
              :itemHeight="itemHeight"
              :focusedObj="focusedObj"
            >
              <span
                v-for="(e, i) in fields"
                :key="i"
                :style="`${item[e + 'Style']};borderBottom: '1px solid ' + theme.tableContentBdColor; width: ${100/fields.length}%;border-right: 1px solid ${theme.tableContentBdColor}`"
              >
                {{item[e] || '--'}}
              </span>
            </slot>
          </div>
        </template>
        <template v-else>
          <slot name="loading"></slot>
        </template>
      </div>
    </div>
    <div
      class="table-scroller"
      v-if="showScroller"
      :style="{backgroundColor: theme.scrollerBgColor2, color: theme.scrollerBgColor2}"
    >
      <div
        class="scroller-top"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: 'transparent transparent'+ theme.scrollerBgColor3 +' transparent'}"></i></div>
      <div
        class="scroller-bottom"
        ref="scrollerBottom"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: theme.scrollerBgColor3 + ' transparent transparent transparent'}"></i></div>
      <div
        class="scroller-scrolling"
        ref="scroller"
        :style="{backgroundColor: theme.scrollerBgColor1}"
        @mousedown="scrolling"
      ></div>
    </div>
  </div>
</template>
<script>
import utils from '../../utils/index';
export default {
  name: 'static-small-list',
  props: {
    theme: Object,
    // 局部列表的展示字段
    fields: {
      type: Array,
      default() {
        return ['Obj', 'ZhongWenJianCheng', 'ZhangDie'];
      },
    },
    // 是否显示表头
    showTitle: {
      type: Boolean,
      default: true,
    },
    title: Object,
    titleAlign: {
      type: String,
      validator(value) {
        return utils.oneOf(value, ['left', 'center', 'right']);
      },
      default: 'center',
    },
    contentAlign: {
      type: String,
      validator(value) {
        return utils.oneOf(value, ['left', 'center', 'right']);
      },
      default: 'center',
    },
    // 整体列表的请求参数
    objReqParam: {
      type: Object,
      default() {
        return {
          gql: 'block=市场/沪深市场/沪深A股',
          // market: 'SH',
          // obj: 'SH600519',
          field: 'ZhongWenJianCheng',
        };
      },
    },
    // 整体列表的默认排序
    desc: {
      type: Number,
      validator(value) {
        return utils.oneOf(value, [0, 1, 2]);
      },
      default: 0, // 0 不排序, 1: true正序, 2: false倒序
    },
    // 整体列表的排序字段
    orderby: {
      type: String,
      default: 'ZhangFu',
    },
    // 是否显示上下滚动条
    showScroller: {
      type: Boolean,
      default: true,
    },
    // 每一份数据的高度
    height: {
      type: Number,
      default: 30,
    },
    // 外部传入 objList 值
    objList: {
      type: Array,
      default() {
        return [];
      },
    },
    // 外部传入初始 focusedObj 值
    FOCUSOBJ: {
      type: String,
      default: '',
    }
  },
  data() {
    return {
      start: 0, // 显示数据的起始下标
      count: 0, // 显示的数据行数
      itemHeight: 0, // 每一行的行高
      dataList: [], // 展示在页面中的list数据
      activeIndex: -1, // 记录当前选中的行的下标
      focusedObj: this.FOCUSOBJ, // 记录当前选中的 Obj
      hoverObj: '',
      i: 1,
    };
  },
  watch: {
    // 监测当前组件的focusedObj变化时, 通知外部组件
    focusedObj(newVal) {
      this.$emit('focused-ischange', newVal);
    },
    // 监测外部组件(如键盘宝)传入的 objList 变化时更新
    objList: {
      handler(newVal) {
        this.objList = newVal;
        this.updateDataList();
      },
      deep: true,
    },
    FOCUSOBJ(newVal) {
      this.focusedObj = newVal;
    },
  },
  methods: {
    init() {
      if (this.init.isInited) return;
      this.init.isInited = true;
      document.addEventListener('keyup', this.keyupFunc);
      document.addEventListener('keydown', this.keydownFunc);
      window.setTimeout(() => {
        // 对父组件传过来的高度进行处理
        let H = this.$refs.smallcontent.offsetHeight;
        this.showTitle && (H -= 30);
        this.$refs.smallcontent.style.height = H + 'px';
        this.count = Math.ceil(H / this.height);
        this.itemHeight = H / this.count;

        if (this.showScroller) {
          this.topMax =
          this.$refs.smalist.offsetHeight -
          this.$refs.scroller.offsetHeight -
          (this.$refs.scrollerBottom.offsetHeight + 1);
          this.topMin =
          this.$refs.scrollerBottom.offsetHeight + 1;
        }
        this.updateDataList();
      });
    },
    updateDataList() {
      for (let i = 0; i < this.count; i++) {
        this.$set(this.dataList, i, this.objList[i + this.start] || {});
      }
    },
    keydownFunc(e) {
      e.preventDefault();
      this.keydown(e.keyCode);
    },
    keyupFunc(e) {
      e.preventDefault();
    },
    destroy() {
      document.removeEventListener('keyup', this.keyupFunc);
      document.removeEventListener('keydown', this.keydownFunc);
      this.init.isInited = false;
    },
    format(item, field) {
      if (!item || JSON.stringify(item) === '{}' || !field) {
        return '--';
      }
      return item[field];
    },
    mouseup(e, obj) {
      this.focusedObj = obj;
      if (e.button === 2) {
        this.$emit('right-click', obj);
      } else {
        this['timer' + this.i] = new Date().getTime();
        this.i += 1;
        if (this.timer1 && this.timer2 && this.timer2 - this.timer1 < 300) {
          if (window.mouseup_timer) {
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
          }
          this.$emit('dbl-click', obj);
          this.i = 1;
          this.timer1 = undefined;
          this.timer2 = undefined;
        } else if (this.timer1 && this.timer2 === undefined) {
          window.mouseup_timer = window.setTimeout(() => {
            this.$emit('left-click', obj);
            this.i = 1;
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
            this.timer1 = undefined;
            this.timer2 = undefined;
          }, 300);
        }
      }
    },
    mousewheel(e) {
      const maxStartVal = this.objList.length - this.count;
      this.start -= Math.round(e.wheelDelta / 120);
      this.start = Math.min(maxStartVal, this.start);
      this.start = Math.max(0, this.start);
      this.changeTopFromStart();
      this.updateDataList();
      if (this.activeIndex !== -1) {
        this.$refs.smalistcontent.childNodes[this.activeIndex].style.backgroundColor = this.theme.tableContentBgColor;
        this.activeIndex = this.dataList.findIndex(
          e => e.DaiMa === this.focusedObj
        );
        if (this.activeIndex !== -1) {
          this.$refs.smalistcontent.childNodes[this.activeIndex].style.backgroundColor = this.theme.tableContentActiveBgColor;
        }
      }
    },
    changeTopFromStart() {
      if (this.showScroller) {
        let bottomHeight = this.$refs.scrollerBottom
          .offsetHeight;
        this.$refs.scroller.style.top =
          (this.start / (this.objList.length - this.count)) *
            (this.topMax - (bottomHeight + 1)) +
          (bottomHeight + 1) +
          'px';
      }
    },
    scrolling(e) {
      let target = e.target;
      // 鼠标点击物体那一刻相对于物体上侧边框的距离=点击时的位置相对于浏览器最上边的距离-物体上边框相对于浏览器最左边的距离
      var diffY = e.clientY - target.offsetTop;
      let mousemove = e => {
        let top = e.clientY - diffY;
        top = Math.max(this.topMin, top);
        top = Math.min(this.topMax, top);
        let scrollerY = top - this.topMin;
        let originStart = this.start;
        let maxStartVal = this.objList.length - this.count;
        // this.start / maxStartVal = scrollerY / (this.topMax - this.topMin)
        this.start = Math.round(
          (scrollerY / (this.topMax - this.topMin)) * maxStartVal
        );
        this.start = Math.min(maxStartVal, this.start);
        this.start !== originStart && this.updateDataList();
        target.style.top = top + 'px';
      };
      let mouseup = () => {
        document.removeEventListener('mousemove', mousemove);
        document.removeEventListener('mouseup', mouseup);
      };
      document.addEventListener('mousemove', mousemove, { passive: true });
      document.addEventListener('mouseup', mouseup);
    },
    // 监听 keydown 事件,
    // 上下键：改变 start 值后, 调用 this.updateDataList, 以更新页面数据 和 滚动条位置, 并更改高亮行
    keydown(code) {
      const maxStartVal = this.objList.length - this.count;
      let originStart = this.start;
      let index;
      switch (code) {
        case 38:
          // 上箭头
          // debugger
          index = this.dataList.findIndex(e => e.DaiMa === this.focusedObj);
          if (index > 0) {
            this.focusedObj = this.dataList[index - 1].DaiMa;
          } else {
            this.start = Math.max(0, this.start - 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
              this.focusedObj = this.dataList[0].DaiMa;
            }
          }
          break;
        case 40:
          // 下箭头
          index = this.dataList.findIndex(e => e.DaiMa === this.focusedObj);
          if (index > -1 && index < this.dataList.length - 1) {
            this.focusedObj = this.dataList[index + 1].DaiMa;
          } else {
            this.start = Math.min(maxStartVal, this.start + 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
              this.focusedObj = this.dataList[this.dataList.length - 1].DaiMa;
            }
          }
          break;
        case 33:
          // 上页
          this.start = Math.max(0, this.start - this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 34:
          // 下页
          this.start = Math.min(maxStartVal, this.start + this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 35:
          // end
          this.start = maxStartVal;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 36:
          // home
          this.start = 0;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        default:
          break;
      }
    },
  },
  created() {
    // console.log('created');
    this.init();
  },
  activated() {
    // console.log('activated');
    this.init();
  },
  deactivated() {
    this.destroy();
  },
  beforeDestroy() {
    this.destroy();
  },
};
</script>
<style lang="less">
@import "../../styles/common.less";
@import "../../styles/static-small-list.less";
</style>
