<template>
  <div class="large-list">
    <div
      class="table-scroll"
      :style="{width: totalWidth + 'px'}"
    >
      <div
        class="table-title"
        :style="{backgroundColor: theme.tableTitleBgColor, color: theme.tableTitleColor, borderBottom: '1px solid ' + theme.tableTitleBdColor, borderTop: '1px solid ' + theme.tableTitleBdColor, height: (titleHeight||30) + 'px'}"
      >
        <div :style="{width: fixedWidth + 'px', height: (titleHeight||30) + 'px', lineHeight: (titleHeight||30) + 'px'}">
          <span
            class="th"
            v-for="(item, index) in fixedFields"
            :key="index"
            :name="item.field"
            @click="reSort"
            :style="`${item.titleAlign?'text-align:'+item.titleAlign+';':''}width:${item.width || 90}px; background-color: ${theme.tableTitleBgColor}; border-right: 1px solid ${theme.tableTitleBdColor};`"
          >
            {{item.title}}
            <i
              v-if="ORDERBY===item.field"
              :class="`iconfont icon-arrow-${DESC === 2?'up':''}${DESC === 1?'down':''}`"
            >
            </i>
            <i
              class="width-changer"
              @mousedown="widthChanging($event, index)"
            ></i>
          </span>
        </div>
        <div
          :style="{width: conWidth + 'px', left: (fixedWidth - x) + 'px', height: (titleHeight||30) + 'px', lineHeight: (titleHeight||30) + 'px'}"
          @dragstart="dragStart"
          @drag="dragging"
          @dragend="dragEnd"
          @drop="drop"
        >
          <span
            class="th"
            v-for="(item, index) in conFields"
            :key="index"
            :name="item.field"
            @click="reSort"
            :style="`${item.titleAlign?'text-align:'+item.titleAlign+';':''}width:${item.width || 90}px; background-color: ${theme.tableTitleBgColor}; border-right: 1px solid ${theme.tableTitleBdColor};`"
            draggable="true"
          >
            {{item.title}}
            <i
              v-if="ORDERBY===item.field"
              :class="`iconfont icon-arrow-${DESC === 2?'up':''}${DESC === 1?'down':''}`"
            >
            </i>
            <i
              class="width-changer"
              :style="{cursor}"
              @mousedown="widthChanging($event, index + fixedFields.length)"
            ></i>
          </span>
        </div>
      </div>
      <div
        class="table-content"
        @mousewheel.passive="mousewheel"
        :style="{backgroundColor: theme.tableContentBgColor, color: theme.tableContentColor, height: contentHeight + 'px'}"
        v-if="dataList[0]"
      >
        <div
          class="table-item"
          v-for="(item, index) in dataList"
          :key="index"
          :name="item.Obj"
          :style="{height: itemHeight + 'px', lineHeight: itemHeight + 'px', borderBottom: '1px solid ' + theme.tableContentBdColor, backgroundColor: focusedObj === item.Obj ? theme.tableContentActiveBgColor: hoverObj === item.Obj ? theme.tableContentHoverBgColor : theme.tableContentBgColor}"
          @contextmenu.prevent
          @mouseup="mouseup($event, item.Obj)"
          @mouseover="hoverObj = item.Obj"
          @mouseleave="hoverObj = ''"
        >
          <list-cell
            :Fields="fixedFields"
            :theme="theme"
            :item="item"
            :index="index"
            :start="start"
            class="item-fixed"
            :style="{width: fixedWidth + 'px', backgroundColor: focusedObj === item.Obj ? theme.tableContentActiveBgColor: hoverObj === item.Obj ? theme.tableContentHoverBgColor : theme.tableContentBgColor}"
          ></list-cell>
          <list-cell
            :Fields="conFields"
            :theme="theme"
            :item="item"
            :index="index"
            :start="start"
            :lastData="item.lastData"
            class="item-unfixed"
            :style="{width: conWidth + 'px', left: (fixedWidth - x) + 'px', backgroundColor: focusedObj === item.Obj ? theme.tableContentActiveBgColor: hoverObj === item.Obj ? theme.tableContentHoverBgColor : theme.tableContentBgColor}"
          >
          </list-cell>
        </div>
      </div>
      <div
        class="table-content"
        v-else
        :style="{height: contentHeight + 'px'}"
      >
        <div
          class="table-item"
          v-for="(item, index) in dataList"
          :key="index"
          :style="{height: itemHeight + 'px', lineHeight: itemHeight + 'px'}"
        >
          <div
            :Fields="fixedFields"
            :theme="theme"
            :item="item"
            :index="index"
            :start="start"
            class="item-fixed"
            :style="{backgroundColor: theme.tableContentBgColor}"
          >
            <span
              v-for="(e, i) in fixedFields"
              :key="i"
              :name="e.field"
              :style="`${e.contentAlign?'text-align:'+e.contentAlign+';':''}width: ${e.width || 90}px;${e.textColor?'color: '+e.textColor + ';':''}border-right:1px solid ${theme.tableContentBdColor};`"
            >--
            </span>
          </div>
          <div
            class="item-unfixed"
            :style="{height: itemHeight + 'px', lineHeight: itemHeight + 'px',width: conWidth + 'px', left: (fixedWidth - x) + 'px',backgroundColor: theme.tableContentBgColor}"
          >
            <span
              v-for="(e, i) in conFields"
              :key="i"
              :name="e.field"
              :style="`${e.contentAlign?'text-align:'+e.contentAlign+';':''}width: ${e.width || 90}px;${e.textColor?'color: '+e.textColor + ';':''}border-right:1px solid ${theme.tableContentBdColor};`"
            >--
            </span>
          </div>
        </div>
      </div>
    </div>
    <!-- 上下滚动条 -->
    <div
      class="table-scroller"
      :style="{backgroundColor: theme.scrollerBgColor2, color: theme.scrollerBgColor2,height: (contentHeight + titleHeight - 9) + 'px'}"
    >
      <div
        class="scroller-top"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: 'transparent transparent'+ theme.scrollerBgColor3 +' transparent'}"></i></div>
      <div
        class="scroller-bottom"
        id="scrollerBottom"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: theme.scrollerBgColor3 + ' transparent transparent transparent'}"></i></div>
      <div
        class="scroller-scrolling"
        id="scroller"
        :style="{backgroundColor: theme.scrollerBgColor1}"
        @mousedown="scrolling"
      ></div>
    </div>
    <!-- 左右滚动条 -->
    <div
      class="table-scroller-lr"
      id="scrollLR"
      :style="{backgroundColor: theme.scrollerBgColor2, width: (contentWidth-9) + 'px'}"
    >
      <div
        class="scroller-left"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: 'transparent ' + theme.scrollerBgColor3 + ' transparent transparent'}"></i></div>
      <div
        class="scroller-right"
        id="scrollerRight"
        :style="{backgroundColor: theme.scrollerBgColor1}"
      ><i :style="{borderColor: 'transparent transparent transparent ' + theme.scrollerBgColor3}"></i></div>
      <div
        class="scroller-scrolling"
        ref="scrollerLR"
        :style="{left: scrollLRLeft + 'px', backgroundColor: theme.scrollerBgColor1}"
        @mousedown="scrollingLR"
      ></div>
    </div>
  </div>
</template>
<script>
import ListCell from './ListCell';
export default {
  name: 'large-list',
  components: { ListCell },
  props: {
    cacheTimer: {
      type: Number,
      default: 1,
    },
    focusedOBJ: {
      type: String,
    },
    // 复杂数据 {"SH600006": {BaoGaoQi:"20190331000000",HangYe:"汽车制造",HuanShou:0.53,...}}
    cacheDATA: {
      type: Object,
      default() {
        return {};
      },
    },
    // 所有页面的排序数组 {'selfStock': [{ 'Obj': 'SH600000', 'ZhongWenJianCheng': '浦发银行', 'XuHao': 0 }, ..., {...}], '上证A股': [{...}]}
    objListCache: {
      type: Object,
      default() {
        return {};
        // return { 'selfStock': [{ 'Obj': 'SH600000', 'ZhongWenJianCheng': '浦发银行', 'XuHao': 0 }, { 'Obj': 'SH600001', 'ZhongWenJianCheng': '邯郸钢铁(退市)' }, { 'Obj': 'SH600002', 'ZhongWenJianCheng': '齐鲁石化(退市)' }, { 'Obj': 'SH600003', 'ZhongWenJianCheng': 'ST东北高(退市)' }, { 'Obj': 'SH600004', 'ZhongWenJianCheng': '白云机场' }, { 'Obj': 'SH600005', 'ZhongWenJianCheng': '武钢股份(退市)' }, { 'Obj': 'SH600006', 'ZhongWenJianCheng': '东风汽车' }, { 'Obj': 'SH600007', 'ZhongWenJianCheng': '中国国贸' }, { 'Obj': 'SH600008', 'ZhongWenJianCheng': '首创股份' }, { 'Obj': 'SH600009', 'ZhongWenJianCheng': '上海机场' }, { 'Obj': 'SH600010', 'ZhongWenJianCheng': '包钢股份' }, { 'Obj': 'SH600011', 'ZhongWenJianCheng': '华能国际' }, { 'Obj': 'SH600012', 'ZhongWenJianCheng': '皖通高速' }, { 'Obj': 'SH600015', 'ZhongWenJianCheng': '华夏银行' }, { 'Obj': 'SH600016', 'ZhongWenJianCheng': '民生银行' }, { 'Obj': 'SH600017', 'ZhongWenJianCheng': '日照港' }, { 'Obj': 'SH600018', 'ZhongWenJianCheng': '上港集团' }, { 'Obj': 'SH600019', 'ZhongWenJianCheng': '宝钢股份' }, { 'Obj': 'SH600020', 'ZhongWenJianCheng': '中原高速' }, { 'Obj': 'SH600021', 'ZhongWenJianCheng': '上海电力' }, { 'Obj': 'SH600022', 'ZhongWenJianCheng': '山东钢铁' }, { 'Obj': 'SH600023', 'ZhongWenJianCheng': '浙能电力' }, { 'Obj': 'SH600025', 'ZhongWenJianCheng': '华能水电' }, { 'Obj': 'SH600026', 'ZhongWenJianCheng': '中远海能' }, { 'Obj': 'SH600027', 'ZhongWenJianCheng': '华电国际' }, { 'Obj': 'SH600028', 'ZhongWenJianCheng': '中国石化' }, { 'Obj': 'SH600029', 'ZhongWenJianCheng': '南方航空' }, { 'Obj': 'SH600030', 'ZhongWenJianCheng': '中信证券' }, { 'Obj': 'SH600031', 'ZhongWenJianCheng': '三一重工' }, { 'Obj': 'SH600033', 'ZhongWenJianCheng': '福建高速' }] };
      },
    },
    fieldsConfig: {
      type: Array,
      required: true,
    },
    theme: {
      type: Object,
      required: true,
    },
    // 整体列表的请求参数
    objReqParam: {
      type: Object,
      default() {
        return {
          gql: 'block=市场/沪深市场/沪深A股',
          field: 'ZhongWenJianCheng',
        };
      },
    },
    // 大列表的默认排序
    desc: {
      type: Number,
      default: 0, // 0 不排序, 1: true正序, 2: false倒序
    },
    orderby: {
      type: String,
      default: 'ZhangFu',
    },
    // 是否显示表头
    showTitle: {
      type: Boolean,
      default: true,
    },
    titleHeight: {
      type: Number,
      default: 30,
    },
    // 是否显示上下滚动条
    showScroller: {
      type: Boolean,
      default: true,
    },
    contentHeight: {
      type: Number,
      default: 700,
    },
    contentWidth: {
      type: Number,
      default: 700,
    },
    signEvent: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      objList: [],
      cacheData: {},
      dataList: [],
      DESC: 0, // 0 不排序, 1: true正序, 2: false倒序
      // orderby: "ZhangFu", // ZuiXinJia
      ORDERBY: '', // ZuiXinJia
      start: 0,
      focusedObj: '',
      count: 20,
      itemHeight: 30,
      hoverObj: '',
      x: 0, // 右侧内容区域偏移量
      cache_x: 0,
      count_x: 0,
      addx: 0,
      topMax: 0,
      leftMax: 0,
      activeIndex: null,
      scrollLRLeft: 10, // 左右滚动条的left属性值
      i: 0,
      cursor: 'e-resize',
      keydownAccess: true,
      focusedIndex: 0,
    };
  },
  watch: {
    // 监测当前组件的查询数组,缓存数据对象,focusedObj变化时, 通知外部组件
    objList: {
      handler(newVal) {
        this.$emit('objlist-ischange', newVal);
      },
      deep: true,
    },
    cacheData: {
      handler(newVal) {
        this.$emit('cachedata-ischange', newVal);
      },
      deep: true,
    },
    focusedObj(newVal) {
      this.$emit('focused-ischange', newVal, this.focusedIndex);
    },
    // 监测外部组件传入的参数变化(如切换tab栏时):
    // 引用外部的objlist到组件中; 重置start; 更新页面数据及滚动条的位置
    objReqParam(newVal, oldVal) {
      this.cancelAll();
      let key = this.$Utils.getKeyFromParams(newVal);
      this.objList = this.objListCache[key] || [];
      this.cacheData = this.cacheDATA;
      this.dataList = [];
      this.start = 0;
      if (this.objList.length) {
        this.updateDataList();
        this.changeTopFromStart();
      } else {
        this.initData();
      }
      this.$nextTick(() => {
        if (!this.objReqParam.obj) {
          this.updateObjListInterval = window.setInterval(() => {
            this.initData();
          }, 5000);
        }
        this.bindDomEvent();
      });
    },
    // 监测外部组件传入的内容区高度改变(如外部触发resize事件)时, 调整展示列数和单列高度
    contentHeight(newVal) {
      let oldCount = this.count;
      this.count = Math.ceil((newVal - 10) / 30);
      this.topMax =
        newVal +
        this.titleHeight -
        document.querySelector('#scroller').offsetHeight -
        document.querySelector('#scrollerBottom').offsetHeight -
        document.querySelector('#scrollLR').offsetHeight;
      if (this.count !== oldCount) {
        this.itemHeight = (newVal - 10) / this.count;
        this.updateDataList();
      }
    },
  },
  computed: {
    totalWidth() {
      return this.fixedWidth + this.conWidth;
    },
    fixedFields() {
      return this.fieldsConfig.filter(e => e.fixed && e.invisible !== true);
    },
    fixedWidth() {
      let width = 0;
      this.fixedFields.forEach(e => {
        width += e.width || 90;
      });
      return width;
    },
    conFields() {
      return this.fieldsConfig.filter(e => !e.fixed && e.invisible !== true);
    },
    conWidth() {
      let width = 0;
      this.conFields.forEach(e => {
        width += e.width || 90;
      });
      return width;
    },
    fields() {
      return this.fieldsConfig.map(e => e.field);
    },
  },
  activated() {
    // console.log('activated');
    this.init();
  },
  created() {
    // console.log('created');
    this.init();
    window.setTimeout(() => {
      this.topMax =
        this.contentHeight +
        this.titleHeight -
        document.querySelector('#scroller').offsetHeight -
        document.querySelector('#scrollerBottom').offsetHeight -
        document.querySelector('#scrollLR').offsetHeight;
      this.leftMax =
        document.querySelector('#scrollLR').offsetWidth -
        document.querySelector('#scrollerRight').offsetWidth * 3;
      this.leftMin = document.querySelector('#scrollerRight').offsetWidth + 1;
    });
  },
  methods: {
    cancelAll() {
      this.cancelQuery();
      this.cancelSubscribe();
      document.removeEventListener('keyup', this.keyupFunc);
      document.removeEventListener('keydown', this.keydownFunc);
      document.ondragover = null;
      document.ondrop = null;
      window.clearInterval(this.updateObjListInterval);
      this.init.isInited = false;
    },
    initState() {
      this.DESC = this.desc;
      this.ORDERBY = this.orderby;

      let count = Math.ceil((this.contentHeight - 10) / 30);
      this.itemHeight = (this.contentHeight - 10) / count;
      this.count = count;
      this.cacheData = this.cacheDATA || {};

      // 根据objlist, focusOBJ 等props 初始化start和focusedObj
      let key = this.$Utils.getKeyFromParams(this.objReqParam);
      this.objList = this.objListCache[key] || [];
      if (this.focusedObj === this.focusedOBJ) return;
      this.focusedObj = this.focusedOBJ;
      this.start = this.initStart(this.objList, this.focusedObj, this.count);
      window.setTimeout(() => {
        this.changeTopFromStart();
      });
    },
    initStart(objList, focusedObj, count) {
      let start = 0;
      if (objList.length === 0) return 0;
      if (focusedObj) {
        start = start > count - 1 ? start : 0;
        start = Math.max(0, objList.findIndex(e => e.Obj === focusedObj)); // 最小值0
        start = Math.min(objList.length - count, start); // 最大值objList.length - count
      }
      return start;
    },
    init() {
      if (this.init.isInited) {
        return;
      }
      this.init.isInited = true;
      // console.log('执行init');
      this.initState();
      this.getObjList()
        .then(res => {
          let oldObjList = JSON.parse(JSON.stringify(this.objList));
          res.forEach((e, i) => {
            this.$set(this.objList, i, e);
          });
          let oldFocusedObjIndex = oldObjList.findIndex(
            e => e.Obj === this.focusedObj
          );
          let newFocusedObjIndex = this.objList.findIndex(
            e => e.Obj === this.focusedObj
          );
          this.objList.length = res.length;
          if (
            this.objList.length > 0 &&
            oldFocusedObjIndex === newFocusedObjIndex
          ) {
            this.start = this.initStart(
              this.objList,
              this.focusedObj,
              this.count
            );
          }
          if (this.objList.length) {
            this.updateDataList();
          } else {
            this.initData();
          }
        })
        .catch(err => {
          console.log('large-list getObjList err: ' + err);
          // this.objList = objList;
          // this.updateDataList();
        });
      if (!this.objReqParam.obj) {
        this.updateObjListInterval = window.setInterval(() => {
          this.initData();
        }, 5000);
      }
      this.bindDomEvent();
    },
    bindDomEvent() {
      this.keyupFunc = e => {
        e.preventDefault();
      };
      this.keydownFunc = e => {
        e.preventDefault();
        let { keyCode, key } = e;
        this.keydown(keyCode, key);
      };
      document.addEventListener('keyup', this.keyupFunc);
      document.addEventListener('keydown', this.keydownFunc);
      // 阻止默认事件, 配合更改拖动时的默认样式
      document.ondragover = function(e) {
        e.preventDefault();
      };
      document.ondrop = function(e) {
        e.preventDefault();
      };
    },
    active(e, index) {
      let childNodes = document.querySelector('.table-content').childNodes;
      if (index === this.activeIndex) return;
      if (this.activeIndex !== null) {
        childNodes[
          this.activeIndex
        ].style.backgroundColor = this.theme.tableContentBgColor;
        childNodes[
          this.activeIndex
        ].childNodes[0].style.backgroundColor = this.theme.tableContentBgColor;
        childNodes[
          this.activeIndex
        ].childNodes[1].style.backgroundColor = this.theme.tableContentBgColor;
      }
      childNodes[
        index
      ].style.backgroundColor = this.theme.tableContentActiveBgColor;
      childNodes[
        index
      ].childNodes[0].style.backgroundColor = this.theme.tableContentActiveBgColor;
      childNodes[
        index
      ].childNodes[1].style.backgroundColor = this.theme.tableContentActiveBgColor;
      this.activeIndex = index;
    },
    hover(e, i) {
      if (this.activeIndex === i) return;
      e.target.style.backgroundColor = this.theme.tableContentHoverBgColor;
      [].slice.call(e.target.childNodes).forEach(childNode => {
        childNode.style.backgroundColor = this.theme.tableContentHoverBgColor;
      });
    },
    leave(e, i) {
      if (this.activeIndex === i) return;
      e.target.style.backgroundColor = this.theme.tableContentBgColor;
      [].slice.call(e.target.childNodes).forEach(childNode => {
        childNode.style.backgroundColor = this.theme.tableContentBgColor;
      });
    },
    drop(ev) {
      ev.dataTransfer.dropEffect = 'move';
      ev.preventDefault();
      ev.stopPropagation();
    },
    dragStart(ev) {
      this.cursor = 'auto';
      // 去除拖动时默认的半透明图像
      let img = new Image();
      img.src = 'drag_image.png';
      ev.dataTransfer.effectAllowed = 'move';
      ev.dataTransfer.setDragImage(img, 10, 10);
      // 生成拖拽过程中的半透明元素
      let field = ev.target.getAttribute('name');
      this.dragItems = [].slice.call(
        document.querySelectorAll(`span[name="${field}"]`)
      );
      let originLeft = (this.dragItems.originLeft = ev.target.getBoundingClientRect().left);
      this.dragItems.clientX_s = ev.clientX;
      this.dragItems.forEach((item, index) => {
        item.cloneItem = item.cloneNode(true);
        item.cloneItem.style.position = 'fixed';
        item.cloneItem.style.height = item.offsetHeight + 'px';
        item.cloneItem.style.top =
          item.parentElement.parentElement.offsetTop + 'px';
        item.cloneItem.style.left = originLeft + 'px';
        item.cloneItem.style.opacity = 0.75;
        item.cloneItem.style.borderLeft = '1px solid #dfe4ea';
        item.cloneItem.style.zIndex = 10;
        item.appendChild(item.cloneItem);
      });
    },
    dragging(ev) {
      ev.preventDefault();
      ev.dropEffect = 'move';
      // 拖拽过程中的半透明元素的位置随鼠标的移动改变
      let left =
        this.dragItems.originLeft +
        ev.clientX -
        this.dragItems.clientX_s +
        'px';
      this.dragItems.forEach(item => {
        item.cloneItem.style.left = left;
      });
      ev.stopPropagation();
    },
    dragEnd(ev) {
      let startIndex = this.fieldsConfig.findIndex(
        e => e.field === ev.target.getAttribute('name')
      );
      ev.preventDefault();
      // 删除拖拽过程中的半透明元素
      this.dragItems.forEach(item => {
        item.removeChild(item.cloneItem);
      });
      // 拖拽结束, 找到结束位置的下标, 改变位置
      let endIndex =
        this.fixedFields.length +
        this.getCountX(
          ev.target.offsetLeft + ev.clientX - this.dragItems.clientX_s
        ).count +
        1;
      if (startIndex === endIndex) return;
      let fieldsConfigCopy = JSON.parse(JSON.stringify(this.fieldsConfig));
      let dragConfig = fieldsConfigCopy.splice(startIndex, 1)[0];
      if (endIndex > startIndex) {
        fieldsConfigCopy.splice(endIndex - 1, 0, dragConfig);
      } else if (endIndex < startIndex) {
        fieldsConfigCopy.splice(endIndex, 0, dragConfig);
      }
      this.$emit('fieldsConfig-ischange', fieldsConfigCopy);
      this.cursor = 'e-resize';
      // 删除临时挂载对象
      this.dragItems = null;
    },
    mousewheel(e) {
      let event = e;
      window.requestAnimationFrame(() => {
        const maxStartVal = this.objList.length - this.count;
        this.start -= Math.round(event.wheelDelta / 120);
        this.start = Math.min(maxStartVal, this.start);
        this.start = Math.max(0, this.start);
        this.changeTopFromStart();
        this.updateDataList();
      });
    },
    changeTopFromStart() {
      document.querySelector('#scroller').style.top =
        (this.start / (this.objList.length - this.count)) * (this.topMax - 11) +
        11 +
        'px';
    },
    widthChanging(e, index) {
      let parentElement = e.target.parentElement;
      parentElement.setAttribute('draggable', false);
      this.wideEdit = true;
      let originWidth = parentElement.getBoundingClientRect().width;
      let originClientX = e.clientX;
      let mousemove = e => {
        window.requestAnimationFrame(() => {
          if (this.wideEdit) {
            this.fieldsConfig[index].width =
              originWidth + e.clientX - originClientX;
          }
        });
      };
      let mouseup = e => {
        this.wideEdit = false;
        parentElement.setAttribute('draggable', true);
        document.removeEventListener('mousemove', mousemove);
        document.removeEventListener('mouseup', mouseup);
      };
      document.addEventListener('mousemove', mousemove, { passive: true });
      document.addEventListener('mouseup', mouseup);
    },
    scrollingLR(e) {
      let target = e.target;
      // 鼠标点击物体那一刻相对于物体左侧边框的距离=点击时的位置相对于浏览器最左边的距离-物体上边框相对于浏览器最左边的距离
      var diffX = e.clientX - target.offsetLeft;
      let mousemove = e => {
        let left = e.clientX - diffX;
        left = Math.max(this.leftMin, left);
        left = Math.min(this.leftMax, left);
        this.scrollLRLeft = left;
        // (left-this.leftMin)/(this.leftMax-this.leftMin) = this.x/(this.totalWidth - this.conFields[this.conFields.length - 1].width)
        let x =
          ((left - this.leftMin) / (this.leftMax - this.leftMin)) *
          (this.conWidth - this.conFields[this.conFields.length - 1].width);
        let res = this.getCountX(x);
        this.x = res.left;
        this.count_x = res.count;
      };
      let mouseup = () => {
        document.removeEventListener('mousemove', mousemove);
        document.removeEventListener('mouseup', mouseup);
      };
      document.addEventListener('mousemove', mousemove, { passive: true });
      document.addEventListener('mouseup', mouseup);
    },
    getCountX(left) {
      let l = 0;
      let count = 0;
      let remain = 0;
      for (let i = 0, Len = this.conFields.length; i < Len; i++) {
        let nextWidth = this.conFields[i].width || 90;
        if (l + nextWidth <= left) {
          l += nextWidth;
          count++;
        } else {
          remain = left - l;
          break;
        }
      }
      return { count, left: l, remain };
    },
    scrolling(e) {
      let target = e.target;
      // 鼠标点击物体那一刻相对于物体上侧边框的距离=点击时的位置相对于浏览器最上边的距离-物体上边框相对于浏览器最左边的距离
      var diffY = e.clientY - target.offsetTop;
      let mousemove = e => {
        let top = e.clientY - diffY;
        top = Math.max(11, top);
        top = Math.min(this.topMax, top);
        let scrollerY = top - 11;
        let originStart = this.start;
        this.start = Math.round(
          (scrollerY / (this.topMax - 11)) *
            (this.objList.length - this.count + 1)
        );
        const maxStartVal = this.objList.length - this.count;
        this.start = Math.min(maxStartVal, this.start);
        this.start !== originStart && this.updateDataList();
        target.style.top = top + 'px';
      };
      let mouseup = () => {
        document.removeEventListener('mousemove', mousemove);
        document.removeEventListener('mouseup', mouseup);
      };
      document.addEventListener('mousemove', mousemove, { passive: true });
      document.addEventListener('mouseup', mouseup);
    },
    initData() {
      this.getObjList()
        .then(res => {
          res.forEach((e, i) => {
            this.$set(this.objList, i, e);
          });
          this.updateDataList();
        })
        .catch(err => {
          console.log('large-list getObjList err: ' + err);
          // this.objList = objList;
          // this.updateDataList();
        });
    },
    // 根据 外部传递props中的 reqParam, orderby 和 DESC 返回排序后的长列表
    getObjList() {
      let indexsParams;
      if (!this.objReqParam.obj) {
        if (this.DESC === 0) {
          indexsParams = Object.assign(this.objReqParam, {
            desc: false,
            orderby: 'JiaoYiDaiMa',
          });
        } else {
          indexsParams = Object.assign(this.objReqParam, {
            desc: this.DESC === 1,
            orderby: this.ORDERBY,
          });
        }
      } else {
        indexsParams = this.objReqParam;
      }
      // console.log(indexsParams);
      return new Promise((res, rej) => {
        this.dataQuery = this.$dzhyun.query('/stkdata', indexsParams, data => {
          if (data && !(data instanceof Error)) {
            res(data);
          } else {
            rej(data);
          }
        });
      });
    },
    // 根据 this.start 和 this.count 渲染出当前屏数据:
    // 1. 数据拿到之前, 先从缓存中得到对应数据并渲染
    // 2. 数据拿到之后, 如果是多条数据, 直接更新缓存中的数据同时渲染页面数据
    //    如果是单条数据, 只更新缓存中的数据, 100ms 后在从缓存中得到页面数据
    updateDataList() {
      this.cancelSubscribe();
      this.objArr = this.objList
        .slice(this.start, this.start + this.count)
        .map(e => e.Obj);
      let indexsParams = {
        obj: this.objArr.join(','),
        field: this.fields,
      };
      this.setDataFromCache();
      this.dataSubscribe = this.$dzhyun.subscribe(
        '/stkdata',
        indexsParams,
        data => {
          if (data && !(data instanceof Error)) {
            if (data.length === 1) {
              let CacheDataItem = data[0];
              CacheDataItem.lastData = this.cacheData[CacheDataItem.Obj];
              this.setCacheData(CacheDataItem);
              if (window.cacheTimer) {
                window.clearTimeout(window.cacheTimer);
                window.cacheTimer = null;
              }
              window.cacheTimer = window.setTimeout(() => {
                this.setDataFromCache();
              }, this.cacheTimer);
              return;
            }
            for (let i = 0, l = data.length; i < l; i++) {
              let newDataItem = data[i];
              this.setDataItem(newDataItem);
              this.setCacheData(newDataItem);
            }
          }
        }
      );
    },
    setDataFromCache() {
      this.objArr.forEach((e, i) => {
        let dataItem = this.dataList.find(
          dataItem => dataItem && dataItem.Obj === e
        );
        let cacheItem = this.cacheData[e];
        if (
          dataItem &&
          cacheItem &&
          dataItem.ZuiXinJia !== cacheItem.ZuiXinJia
        ) {
          cacheItem.ZuiXinJiaStyle = cacheItem.ZhangDieStyle = cacheItem.ZhangFuStyle = `animation: twinkling${
            dataItem.ZuiXinJia > cacheItem.ZuiXinJia ? 'Green' : 'Red'
          } 1s ease-in 0s;`;
        }
        this.$set(this.dataList, i, cacheItem || {});
      });
      this.dataList.length = this.objArr.length;
    },
    setDataItem(newDataItem) {
      let index = this.objArr.findIndex(e => e === newDataItem.Obj);
      this.$set(this.dataList, index, newDataItem);
    },
    setCacheData(newDataItem) {
      this.$set(this.cacheData, newDataItem.Obj, newDataItem);
    },
    // 监听 keydown 事件,
    // 上下键：改变 start 值后, 调用 this.updateDataList, 以更新页面数据 和 滚动条位置, 并更改高亮行
    keydown(code, key) {
      if (this.signEvent) return;
      const maxStartVal = this.objList.length - this.count;
      let originStart = this.start;
      let index;
      switch (code) {
        case 38:
          // 上箭头
          index = this.objArr.findIndex(e => e === this.focusedObj);
          if (index > 0) {
            this.focusedObj = this.objArr[index - 1];
            this.focusedIndex = this.start + index - 1;
          } else {
            this.start = Math.max(0, this.start - 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
            }
            if (index === 0) {
              this.focusedObj = this.objArr[0];
              this.focusedIndex = this.start;
            }
          }
          break;
        case 40:
          // 下箭头
          index = this.objArr.findIndex(e => e === this.focusedObj);
          if (index > -1 && index < this.objArr.length - 1) {
            this.focusedObj = this.objArr[index + 1];
            this.focusedIndex = this.start + index + 1;
          } else {
            this.start = Math.min(maxStartVal, this.start + 1);
            if (this.start !== originStart) {
              this.changeTopFromStart();
              this.updateDataList();
            }
            if (index === this.objArr.length - 1) {
              this.focusedObj = this.objArr[this.objArr.length - 1];
              this.focusedIndex = this.start + this.objArr.length - 1;
            }
          }
          break;
        case 33:
          // 上页
          this.start = Math.max(0, this.start - this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 34:
          // 下页
          this.start = Math.min(maxStartVal, this.start + this.count);
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 35:
          // end
          this.start = maxStartVal;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 36:
          // home
          this.start = 0;
          if (this.start !== originStart) {
            this.changeTopFromStart();
            this.updateDataList();
          }
          break;
        case 37:
          // 左箭头
          // if (this.count_x > 0) {
          //   this.cache_x--;
          //   this.count_x--;
          //   this.addx += this.conFields[this.count_x].width || 90;
          // }
          // if (window.cacheXTimer) {
          //   window.clearTimeout(window.cacheXTimer);
          //   window.cacheXTimer = null;
          // }
          // window.cacheXTimer = window.setTimeout(() => {
          //   this.x -= this.addx;
          //   this.cache_x = 0;
          //   this.addx = 0;
          // }, 30);
          if (this.count_x > 0) {
            let x = this.x;
            let res = this.getCountX(x);
            if (res.remain > 0) {
              this.x = res.x;
            } else {
              this.x =
                res.left - (this.conFields[this.count_x - 1].width || 90);
            }
            this.count_x--;
            this.scrollLRLeft =
              (this.x /
                (this.conWidth -
                  this.conFields[this.conFields.length - 1].width)) *
                (this.leftMax - this.leftMin) +
              this.leftMin;
          }
          break;
        case 39:
          // 右箭头
          // if (this.count_x + this.cache_x < this.conFields.length - 1) {
          //   this.addx += this.conFields[this.count_x].width || 90;
          //   this.cache_x++;
          //   this.count_x++;
          // }
          // if (window.cacheXTimer) {
          //   window.clearTimeout(window.cacheXTimer);
          //   window.cacheXTimer = null;
          // }
          // window.cacheXTimer = window.setTimeout(() => {
          //   this.x += this.addx;
          //   this.cache_x = 0;
          //   this.addx = 0;
          // }, 10);

          if (this.count_x + 1 < this.conFields.length) {
            let x = this.x;
            let res = this.getCountX(x);
            this.x = res.left + (this.conFields[this.count_x].width || 90);
            this.count_x++;
            // (left-this.leftMin)/(this.leftMax-this.leftMin) = this.x/(this.totalWidth - this.conFields[this.conFields.length - 1].width)
            this.scrollLRLeft =
              (this.x /
                (this.conWidth -
                  this.conFields[this.conFields.length - 1].width)) *
                (this.leftMax - this.leftMin) +
              this.leftMin;
          }
          break;
        default:
          break;
      }
    },
    mouseup(e) {
      let obj = e.target.parentElement.parentElement.getAttribute('name');
      this.focusedObj = obj;
      this.focusedIndex = this.objArr.indexOf(obj) + this.start;
      if (e.button === 2) {
        this.$emit('right-click', obj);
      } else {
        this['timer' + this.i] = new Date().getTime();
        this.i += 1;
        if (this.timer1 && this.timer2 && this.timer2 - this.timer1 < 300) {
          if (window.mouseup_timer) {
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
          }
          this.$emit('dbl-click', obj);
          this.i = 1;
          this.timer1 = undefined;
          this.timer2 = undefined;
        } else if (this.timer1 && this.timer2 === undefined) {
          window.mouseup_timer = window.setTimeout(() => {
            this.$emit('left-click', obj);
            this.i = 1;
            window.clearTimeout(window.mouseup_timer);
            window.mouseup_timer = null;
            this.timer1 = undefined;
            this.timer2 = undefined;
          }, 300);
        }
      }
    },
    async reSort(e) {
      if (this.objReqParam.obj) {
        return;
      }
      let orderby = e.target.getAttribute('name');
      if (this.ORDERBY === orderby) {
        switch (this.DESC) {
          case 0:
            this.DESC = 1;
            break;
          case 1:
            this.DESC = 2;
            break;
          case 2:
            this.DESC = 0;
            break;
          default:
            break;
        }
      } else {
        this.ORDERBY = orderby;
        this.DESC = 1;
      }
      await this.initData();
      let originStart = this.start;
      this.start = 0;
      if (this.start !== originStart) {
        this.changeTopFromStart();
        this.updateDataList();
      }
    },
    cancelQuery() {
      if (this.dataQuery) {
        this.dataQuery.cancel();
        this.dataQuery = null;
      }
    },
    cancelSubscribe() {
      if (this.dataSubscribe) {
        this.dataSubscribe.cancel();
        this.dataSubscribe = null;
      }
    },
  },
  deactivated() {
    // console.log('deactivated');
    this.cancelAll();
  },

  beforeDestroy() {
    // console.log('beforeDestroy');
    this.cancelAll();
  },
};
</script>
<style lang="less">
@import "../../styles/common.less";
@import "../../styles/large-list.less";
</style>
