<template>
  <div class="smallist-demo">
    <static-small-list
      v-if="searchResult.length>0"
      :theme="theme"
      :fields="fields"
      :showTitle="false"
      :titleAlign="titleAlign"
      :showScroller="true"
      :height=30
      :FOCUSOBJ="focusedObj"
      :objList="searchResult"
      @right-click="rightclick"
      @dbl-click="dblclick"
      @left-click="leftclick"
      @focused-ischange="focusedObjIschange"
    >
      <template slot-scope="slotScopes">
        <span
          v-for="(e, i) in fields"
          :key="i"
          :style="`width: ${100/fields.length}%;`"
        >
          {{slotScopes.data[e] || '--'}}
        </span>
      </template>
      <template slot="loading">
        <p>loading content</p>
      </template>
    </static-small-list>
  </div>
</template>

<script>
import { theme, title } from '../utils/fieldsConfig.js';
export default {
  name: 'static-smallist-demo',
  data() {
    return {
      fields: ['DaiMa', 'MingCheng', 'ShuXing'],
      titleAlign: 'left',
      theme,
      title,
      focusedObj: '',
      searchResult: [],
    };
  },
  created() {
    this.showSearchResult();
  },
  methods: {
    rightclick(obj) {
      console.log('right-click', obj);
    },
    dblclick(obj) {
      console.log('dbl-click', obj);
    },
    leftclick(obj) {
      console.log('left-click', obj);
    },
    focusedObjIschange(focuseItem, focusIndex) {
      this.focusedObj = focuseItem.DaiMa;
    },
    showSearchResult(searchText) {
      this.$dzhyun.query(
        '/kbspirit',
        {
          input: '6',
          kuozhan: 0,
          count: 80,
          type: '6',
          market: 'SH,SZ,B$',
        },
        data => {
          if (data && !(data instanceof Error) && data[0].JieGuo) {
            this.searchResult = data[0].JieGuo[0].ShuJu;
            this.focusedObj = this.searchResult[0].DaiMa;

            // this.activeIndex = 0;
            // this.activeObj = this.searchResult[0].DaiMa;
            // console.log(this.searchResult);
          }
        }
      );
    },
  },
};
</script>
<style lang="less">
.smallist-demo {
  width: 270px;
  height: 600px;
}
</style>
